@font-face { font-family: 'NanumBarunpen Bold'; src: url(./fonts/NanumBarunpenB.ttf) format('truetype'); }

.Main {
    
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
}


.MainContext {
    align-items: center;

    display: flex;
    flex-direction: column;
    
    height: 100%;
    width: 100%;
}

.imgBG {
    position: absolute;

    height: 100%;
    width: auto;
}

.NumberBox {
    position: absolute;
    border: 2px solid red;

    width: 6.2vh;
    height: 4.6vh;

    margin-top: 2.3vh;
}

.Number {
    position: absolute;

    text-align: left;

    color: white;

    font-family: 'NanumBarunpen Bold';

    margin-left: 1.2vh;
    margin-top: -0.5vh;

    font-size: 4vh;
}

.Title {
    position: absolute;


    color: black;

    font-family: 'NanumBarunpen Bold';
    line-height: 3.8vh;

    margin-top: 10vh;

    font-size: 3vh;

    max-width: 45vh;
    max-height: 7.8vh;
}